import PNotify from 'pnotify/dist/es/PNotify.js';
import 'pnotify/dist/PNotifyBrightTheme.css';

export default {
  changeName() {
    PNotify.success({
      text: '    ',
      delay: 3000,
    });
  },
  changeTelegramName() {
    PNotify.success({
      text: '  Telegram ',
      delay: 3000,
    });
  },
  changeEmail() {
    PNotify.success({
      text: ' Email   ',
      delay: 3000,
    });
  },

  error(textContent) {
    PNotify.error({
      text: textContent,
      delay: 3000,
    });
  },
};
